import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { setLoggedInUser } from "../../store/actions/userActions";
import NiceButton from "../NiceButton/NiceButton";
import contactLogo from "../../assets/icons/users.png";
import backBtnLogo from "../../assets/icons/back.png";

export const AppFooter = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser);
  }, [loggedUser, dispatch]);

  const onGoContacts = () => {
    history.push("/contact");
  };

  const onGoBack = () => {
    history.goBack();
  };

  const IconGoContacts = () => <img src={contactLogo} alt="" />;
  const IconGoBack = () => <img src={backBtnLogo} alt="" />;

  return (
    loggedUser && (
      <div className="app-footer">
        <NiceButton
          className="go-back-btn"
          Icon={IconGoBack}
          onClick={onGoBack}
        />
        <NiceButton
          className="contacts-btn"
          Icon={IconGoContacts}
          onClick={onGoContacts}
        />
      </div>
    )
  );
};
