import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { useSelector } from "react-redux";

export const TransfersTable = ({ rows, title }) => {
  const loggedUser = useSelector((state) => state.userReducer.user);

  return (
    <TableContainer component={Paper}>
      <h2>{title}</h2>
      <Table sx={{ minWidth: 650 }} aria-label="sticky table">
        <TableHead>
          <TableRow hover role="checkbox">
            <TableCell>Date</TableCell>
            <TableCell>From</TableCell>
            <TableCell>To</TableCell>
            <TableCell>Amount</TableCell>
            <TableCell>Balance</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              hover={true}
              key={row._id}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell>
                {new Date(row.createdAt).toLocaleDateString()}
              </TableCell>
              <TableCell>{row.from}</TableCell>
              <TableCell>{row.to}</TableCell>
              <TableCell
                className={
                  loggedUser.email === row.to ? "amount-plus" : "amount-minus"
                }
              >
                {loggedUser.email === row.to
                  ? `+${row.amount}`
                  : `-${row.amount}`}
              </TableCell>
              <TableCell>
                {loggedUser.email === row.to
                  ? `${(loggedUser.coins + row.amount).toFixed(2)} ₿`
                  : `${(loggedUser.coins - row.amount).toFixed(2)} ₿`}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
