import { useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../../store/actions/userActions";
import homeLogo from "../../assets/icons/home.png";
import { AccountMenu } from "../AccountMenu";
import NiceButton from "../NiceButton/NiceButton";

export const AppHeader = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const history = useHistory();
  const dispatch = useDispatch();

  const onGoHome = () => history.push("/");

  const onLogout = () => {
    dispatch(logout());
    history.push("/");
  };
  const IconHome = () => <img src={homeLogo} alt="" />;

  return (
    <header className="app-header full">
      <NiceButton Icon={IconHome} onClick={onGoHome} className="home-logo" />

      <AccountMenu
        loggedUser={loggedUser}
        logout={onLogout}
        history={history}
      />
    </header>
  );
};
