import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { saveContact } from "../../store/actions/contactActions";
import { uploadImg } from "../../services/imgUploadService";
import { contactService } from "../../services/contactService";
import { setLoggedInUser } from "../../store/actions/userActions";
import deleteBtnLogo from "../../assets/icons/delete-friend.png";
import NiceButton from "../../cmps/NiceButton/NiceButton";

export const ContactEditPage = (props) => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const [contact, setContact] = useState(null);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser());
  }, [dispatch, loggedUser]);

  useEffect(() => {
    const loadContact = async (id) => {
      const contact = id
        ? await contactService.getContactById(id)
        : contactService.getEmptyContact();
      setContact(contact);
    };
    loadContact(props.match.params.id);
  }, [props.match.params.id]);

  const handleChange = async (ev) => {
    const field = ev.target.name;
    var value;
    if (ev.target.type === "file") {
      value = (await uploadImg(ev)).url;
    } else {
      value = ev.target.type === "number" ? +ev.target.value : ev.target.value;
    }
    setContact({ ...contact, [field]: value });
  };

  const onRemoveContact = async () => {
    await contactService.deleteContact(contact._id);
    props.history.push("/contact");
  };

  const onSaveContact = (ev) => {
    ev.preventDefault();
    dispatch(saveContact(contact));
    props.history.push("/contact");
  };
  const IconDelete = () => <img src={deleteBtnLogo} alt="" />;

  if (!contact) return <h1>Contact not found</h1>;
  return (
    <div className="contact-edit">
      <form onSubmit={onSaveContact}>
        <div className="contact-edit contact-name">
          <label htmlFor="contactName">Fullname</label>
          <input
            required
            type="text"
            id="contactName"
            value={contact.contactName}
            onChange={handleChange}
            name="contactName"
          />
        </div>

        <div className="contact-edit contact-email">
          <label htmlFor="contactEmail">Email Address</label>
          <input
            required
            type="text"
            id="contactEmail"
            value={contact.contactEmail}
            onChange={handleChange}
            name="contactEmail"
          />
        </div>

        <div className="contact-edit contact-phone">
          <label htmlFor="contactPhone">Phone Number</label>
          <input
            required
            type="text"
            id="contactPhone"
            value={contact.contactPhone}
            onChange={handleChange}
            name="contactPhone"
          />
        </div>

        <div className="contact-edit contact-img">
          <label htmlFor="contactImg">Image</label>
          <input
            // required
            type="file"
            id="contactImg"
            // value={contact.contactImg}
            onChange={handleChange}
            name="contactImg"
          />
        </div>
        <button>Save Contact</button>
        {contact._id && (
          <NiceButton
            Icon={IconDelete}
            onClick={onRemoveContact}
            className="delete-btn"
          />
        )}
      </form>
    </div>
  );
};
