import * as React from "react";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import ListItemIcon from "@mui/material/ListItemIcon";
// import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import Tooltip from "@mui/material/Tooltip";
import Logout from "@mui/icons-material/Logout";
import Login from "@mui/icons-material/Login";
import { Typography } from "@mui/material";

export const AccountMenu = ({ loggedUser, logout, history }) => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const amountClass = loggedUser?.coins ? "amount-plus" : "amount-minus";

  return (
    <React.Fragment>
      <Box sx={{ display: "flex", alignItems: "center", textAlign: "center" }}>
        {loggedUser && (
          <div className="details-header">
            <Typography>
              Balance:{" "}
              <span className={amountClass}>{loggedUser.coins?.toFixed(2)}</span>{" "}
              ₿
            </Typography>
            <Typography>{loggedUser.email}</Typography>
          </div>
        )}
        <Tooltip title="Profile">
          <IconButton onClick={handleClick} size="small" sx={{ ml: 2 }}>
            {loggedUser ? (
              <Avatar alt={loggedUser.fullname} src={loggedUser.img} />
            ) : (
              <Avatar />
            )}
          </IconButton>
        </Tooltip>
      </Box>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        PaperProps={{
          elevation: 0,
          sx: {
            overflow: "visible",
            filter: "drop-shadow(0px 2px 8px rgba(0,0,0,0.32))",
            mt: 1.5,
            "& .MuiAvatar-root": {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            "&:before": {
              content: '""',
              display: "block",
              position: "absolute",
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: "background.paper",
              transform: "translateY(-50%) rotate(45deg)",
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: "right", vertical: "top" }}
        anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
      >
        {loggedUser && (
          <MenuItem onClick={() => history.push("/user")}>
            <Avatar />
            Profile
          </MenuItem>
        )}
        {loggedUser && (
          <MenuItem onClick={logout}>
            <ListItemIcon>
              <Logout fontSize="small" />
            </ListItemIcon>
            Logout
          </MenuItem>
        )}
        {!loggedUser && (
          <MenuItem onClick={() => history.push("/signup/login")}>
            <ListItemIcon>
              <Login fontSize="small" />
            </ListItemIcon>
            Login
          </MenuItem>
        )}
        {!loggedUser && (
          <MenuItem onClick={() => history.push("/signup")}>SignUp</MenuItem>
        )}
      </Menu>
    </React.Fragment>
  );
};
