export const login = (userCred) => {
  return async (dispatch) => {
    const user = await userService.login(userCred);
    dispatch({ type: "SET_USER", user });
  };
};

export const setLoggedInUser = () => {
  return async (dispatch) => {
    const loggedInUser = await userService.getLoggedInUser();
     dispatch({ type: "SET_USER", user: loggedInUser });
  };
};

export const logout = () => {
  return async (dispatch) => {
    await userService.logout();
    dispatch({ type: "LOGOUT" });
  };
};

export const transferCoins = (contactEmail, amount) => {
  return async (dispatch) => {
    const updatedUser = await transferService.addTransfer(contactEmail, amount);
    dispatch({ type: "SET_USER", user: updatedUser });
  };
};
