import { useEffect, useState } from "react";
import { bitcoinService } from "../../services/bitcoinService.js";
import { useDispatch, useSelector } from "react-redux";
import { setLoggedInUser } from "../../store/actions/userActions.js";
import { transferService } from "../../services/transferService.js";
import { TransfersTable } from "../../cmps/TransfersTable/TransfersTable.jsx";
import { RegisterBtn } from "../../cmps/RegisterBtn/RegisterBtn.jsx";

export const HomePage = ({ history }) => {
  const [rateByDollar, setBitcoinRate] = useState(0);
  const [isAllTransfers, setIsAllTransfers] = useState(true);
  const loggedUser = useSelector((state) => state.userReducer.user);
  const [transfers, setTransfers] = useState(null);

  const dispatch = useDispatch();

  const rateByBitcoin = Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(1 / rateByDollar);

  const totalValue = loggedUser
    ? Intl.NumberFormat("en-US", {
        style: "currency",
        currency: "USD",
        minimumFractionDigits: 0,
        maximumFractionDigits: 0,
      }).format((1 / rateByDollar) * loggedUser.coins)
    : null;

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser());
  }, [loggedUser, dispatch]);

  useEffect(() => {
    const getTransfers = async () => {
      const transfers = await transferService.getTransfers();
      if (transfers.length) setTransfers(transfers);
    };
    if (loggedUser) getTransfers();
  }, [loggedUser]);

  useEffect(() => {
    const loadBitcoinRate = async () => {
      const fetchRate = await bitcoinService.getRate();
      setBitcoinRate(fetchRate);
    };
    loadBitcoinRate();
  }, [rateByDollar]);

  const slicedTransfers = transfers?.slice(0, 3);

  const transfersToShow = isAllTransfers ? slicedTransfers : transfers;
  const transferFilterBtnText = isAllTransfers ? "See all.." : "See less...";
  const tableTitle = isAllTransfers ? "Last 3 transfers:" : "All transfers:";
  const amountClass = loggedUser?.coins ? "amount-plus" : "amount-minus";

  return loggedUser ? (
    <div className="home-page logged-user">
      <div className="user-balance">
        <h5>Wallet Balance:</h5>
        <h3 className={amountClass}>{totalValue}</h3>
        <h4>
          You have <span className={amountClass}>{loggedUser.coins}</span>{" "}
          bitcoins
        </h4>
        <h5>1 Bitcoin: {rateByBitcoin}</h5>
      </div>
      {transfers && (
        <div className="transfers-table">
          {transfers.length > 3 && (
            <p onClick={() => setIsAllTransfers((prev) => !prev)}>
              {transferFilterBtnText}
            </p>
          )}

          <TransfersTable title={tableTitle} rows={transfersToShow} />
        </div>
      )}
    </div>
  ) : (
    <div className="home-page guest">
      <div className="guest-welcome">
        <h1>Welcome to Mister Bitcoin</h1>
        <h3>Demo bitcoin wallet</h3>
        <RegisterBtn />
        <p onClick={() => history.push("/signup/login")}>
          Already registered? login
        </p>
      </div>
    </div>
  );
};
